<script>
import mapboxgl from "mapbox-gl";
import rightStickIcon from "../../components/rightStickIcon";
import axios from "axios";
//marker;
// map;
export default {
  components: {
    rightStickIcon,
  },

  data() {
    return {
      MyaccessToken:
        "pk.eyJ1IjoiZ2lyaXNoaGFsZGFua2FyIiwiYSI6ImNsam1sdXJsMzAyZjYzcWtwcjA0dWQ1MTgifQ.Kh5i1c1sOmfv3SaAY7SpMw",

      placesData: [
        // {
        //   description: "Chennai",
        //   coordinates: [80.21117527116763, 13.008852534856274],
        // },
        // {
        //   description: "Dubai",
        //   coordinates: [55.29004174850411, 25.280927196903818],
        // },
        // {
        //   description: "America",
        //   coordinates: [-77.090372, 38.881189],
        // },
      ],
      airlineDetail: {},
      contact: "",
      email: "",
      officeAddress: "",
      importantLinks: [],
      mediaLink: [],
      policys: [],
      airlineList: [],
    };
  },
  methods: {
    setupMapBox(center = [80.16684476066258, 12.983596468763904]) {
      console.log("center", center);
      mapboxgl.accessToken = this.MyaccessToken;
      this.map = new mapboxgl.Map({
        container: "map",

        //style: 'mapbox://styles/mapbox/streets-v9',
        style: "mapbox://styles/mapbox/light-v11",
        zoom: 1.6,
        attributionControl: false,
        projection: {
          name: "equirectangular",
          center: center,
        },
      });
    },
    markers(coordinates = [], title = "") {
      console.log("coordinates", coordinates);
      console.log("title", title);
      if (coordinates.length) {
        const popup = new mapboxgl.Popup({ offset: 25 }).setText(
          ` name :${title}`
        );
        this.marker1 = new mapboxgl.Marker({ color: "#0072FF" })
          .setLngLat(coordinates)
          .setPopup(popup) // sets a popup on this marker
          .addTo(this.map);
        // const markerDiv = this.marker1.getElement();

        // markerDiv.addEventListener("mouseenter", () => this.marker1.togglePopup());
        // markerDiv.addEventListener("mouseleave", () => this.marker1.togglePopup());
      }
    },
    markerPopup() {
      this.popup = new mapboxgl.Popup({
        closeButton: false,
        closeOnClick: false,
      });
    },

    rowClick(row) {
      this.$router.push("airlinedetails/");
      console.log(row.name);
    },
    async getAirlineInfo() {
      console.log("getAirlineInfo");
      console.log(this.$store.getters.getAirlineData);
      let airline = this.$store.getters.getAirlineData;
      console.log("airline ", airline);
      let airlineDetail = await axios.post(
        `${process.env.VUE_APP_AIRLlINE_URL}/user/airline/detail`,
        {
          code: airline.code,
          language: airline.language,
        }
      );
      console.log("airlineList ", airlineDetail.data.data);

      this.airlineDetail = airlineDetail.data.data;

      this.airlineDetail.contacts.forEach((c) => {
        if ((c.phone.code != undefined) & (c.phone.no != undefined))
          this.contact += c.phone.code + "  " + c.phone.no + ",";
      });

      this.airlineDetail.emails.forEach((e) => {
        console.log("email ", e);
        if (e.address != undefined) this.email += e.address + ",";
      });
      this.airlineDetail.offices.forEach((o) => {
        this.officeAddress =
          o.address.city + " " + o.address.state + " " + o.address.pincode;
      });

      this.airlineDetail.links.forEach((x) =>
        (x.type != "SOCIAL_MEDIA" ? this.importantLinks : this.mediaLink).push(
          x
        )
      );
      this.policys = this.importantLinks.filter((p) => p.type == "POLICY");
      this.importantLinks = this.importantLinks.filter(
        (p) => p.type != "POLICY"
      );
      console.log("this.importantLinks ", this.importantLinks);

      console.log("this.mediaLink ", this.mediaLink);

      console.log("policys", this.policys);
      this.placesData = this.airlineDetail.destinations;
      this.setupMapBox([
        this.airlineDetail.destinations[0].coordinates.longitude,
        this.airlineDetail.destinations[0].coordinates.latitude,
      ]);
      this.airlineList = this.airlineDetail.destinations;
      this.airlineDetail.destinations.forEach((dest) => {
        console.log("dest", dest);
        // this.markers([dest.coordinates.latitude,dest.coordinates.longitude], dest.name);
        this.markers(
          [dest.coordinates.longitude, dest.coordinates.latitude],
          dest.name
        );
      });
    },
  },

  mounted() {
    this.setupMapBox();
    this.placesData.forEach((markerData) => {
      this.markers(markerData.coordinates);
    });
    this.markers();
    this.markerPopup();

    this.map.on("load", () => {});
    this.getAirlineInfo();
  },
};
</script>
